import React from 'react';
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';
import ScrollToTopOnMount from './ScrollToTopOnMount';
import { twitterUser } from '../configs';
import { StaticQuery, useStaticQuery, graphql } from 'gatsby';



//const image = node.frontmatter.socialcard;

export default function Seo({ title, description, keywords = [], meta = [], children, }) {
  /*const { site } = useStaticQuery(graphql`
    query SeoQuery {
      site {
        siteMetadata {
          siteUrl
        }
      }
    }
  `);*/

  const { i18n, t } = useTranslation();
  const metaDescription = description || t('ProjectSlogan');
  const defaultTitle = `${t('ProjectName')} - ${t('ProjectSlogan')}`;
  //const image = `${site.siteMetadata.siteUrl}/ivannikov.pro_${i18n.language}.jpg`;
  const image = `https://ivannikov.pro/ivannikov.pro_${i18n.language}.jpg`;

  return (
    <>
      <ScrollToTopOnMount />
      <Helmet
        htmlAttributes={{
          lang: i18n.language,
        }}

        title={title}
        defaultTitle={defaultTitle}
        titleTemplate={defaultTitle ? `%s | ${t('ProjectName')}` : undefined}

        meta={[
          { name: 'description', content: metaDescription, },
          { name: 'keywords', content: keywords.join(', '), },
          { name: 'image', content: image, },
          { name: 'og:title', property: 'og:title', content: title, },
          { name: 'og:description', property: 'og:description', content: metaDescription, },
          { name: 'og:type', property: 'og:type', content: 'website', },
          { name: 'og:image', property: 'og:image', content: image, },
          { name: 'twitter:card', property: 'twitter:card', content: 'summary_large_image', },
          { name: 'twitter:image', property: 'twitter:image', content: image, },
          { name: 'twitter:image:alt', property: 'twitter:image:alt', content: metaDescription, },
          { name: 'twitter:site', property: 'twitter:site', content: twitterUser ? `@${twitterUser}` : '', },
          { name: 'twitter:creator', property: 'twitter:creator', content: twitterUser ? `@${twitterUser}` : '', },
          { name: 'twitter:title', property: 'twitter:title', content: title, },
          { name: 'twitter:description', property: 'twitter:description', content: metaDescription, },
        ].concat(meta)}
      >
      </Helmet >
      {children}
    </>
  );
}

