import React, { useContext, useState } from 'react';
//import { Visibility } from 'semantic-ui-react';

import { useSelector } from 'react-redux';
import * as S from '../store/selectors';
import { ThemeContext } from '../theme';

import { useMediaPredicate } from 'react-media-hook';
import { mobileBreakpoint } from '../helpers/Media';

import Seo from './Seo';
import Header from './Header';
import MobileMenu from './MobileMenu';
import Footer from './Footer';



export default function Page({ title, description, meta, children, ...props }) {
  const { theme } = useContext(ThemeContext);
  //const theme = useSelector(S.theme.getTheme);

  const isMobile = useMediaPredicate(mobileBreakpoint);
  const isMobileMenuOpen = useSelector(S.mobileMenu.isOpen);
  //const [state, setState] = useState({ fixed: false });
  //const hideFixedMenu = () => setState({ fixed: false });
  //const showFixedMenu = () => setState({ fixed: true });

  // class="theapp page_theme_default page_theme_dark page_theme_light"
  return (
    <div className={`theapp page_theme_default ${theme}`} >
      <Seo
        title={title}
        description={description}
        meta={meta ? meta : []}
      />
      {/*<Visibility
        once={false}
        onBottomPassed={showFixedMenu}
        onBottomPassedReverse={hideFixedMenu}
       >*/}
      <Header />
      {isMobile && isMobileMenuOpen && <MobileMenu />}

      {/*<MobileMenu v-if="mobile" />*/}
      {/*</Visibility>*/}
      {children}
      <Footer />
    </div>
  );
}
