import React, { useContext } from 'react';
import { ThemeContext } from '../theme';
import { SunIcon, MoonIcon, } from './Svg';
import './ThemeSwitcher.scss';



export default function ThemeSwitcher({ className }) {
  const { theme, themes, toggleTheme } = useContext(ThemeContext);
  const onChange = (code) => {
    toggleTheme(code);
  }

  return (
    <div className={`${theme} theme__switcher ${className}`}>
      <div className="theme__switcher-inner">
        <div
          className={`theme__switcher-icon ${theme === themes[1] ? 'theme__switcher-icon-active' : ''}`}
        >
          <SunIcon />
        </div>
        <div
          className={`theme__switcher-icon ${theme === themes[0] ? 'theme__switcher-icon-active' : ''}`}
        >
          <MoonIcon />
        </div>

        {theme === themes[1] && <div
          onClick={() => { toggleTheme(themes[0]) }}
          className="theme__switcher-overlay"
        ></div>}
        {theme === themes[0] && <div
          onClick={() => { onChange(themes[1]) }}
          className="theme__switcher-overlay"
        ></div>}
      </div>
    </div>
  );
}
