import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as ScrollLink, Events as ScrollEvents, } from 'react-scroll';
import { Link, LinkExternal } from './Link';

import { useSelector, useDispatch, } from "react-redux";
import * as S from "../store/selectors";
import * as A from "../store/actions";

import { useMediaPredicate } from 'react-media-hook';
import { mobileBreakpoint } from '../helpers/Media';

//import ReactPixel from 'react-facebook-pixel';
import Logo from './Logo';
import SocialLinks from './SocialLinks';
import ThemeSwitcher from './ThemeSwitcher';
import LangSwitcher from './LangSwitcher.jsx';
import { MobileMenuIcon } from './Svg';
import './Header.scss';



export default function Header() {
  const isMobile = useMediaPredicate(mobileBreakpoint);
  const { i18n, t } = useTranslation('header');
  const isMobileMenuOpen = useSelector(S.mobileMenu.isOpen);
  const dispatch = useDispatch();
  const setMobileMenuOpen = (value) => {
    dispatch(A.mobileMenu.set(value));
  }


  useEffect(() => {
    ScrollEvents.scrollEvent.register('begin', function (to, element) {
      setMobileMenuOpen(false);
    });
    return () => {
      ScrollEvents.scrollEvent.remove('begin');
    };
  }, []);

  return (
    <header className="header">
      <div className="container">
        <div className="row justify-content-between align-items-center">
          <div className="col-auto">
            <Logo className="appheader-logo" />
          </div>
          <div className="d-none d-lg-block col">
            <nav className="nav">
              {/*<Link to='/about' style={{ cursor: 'pointer', }}>
                {t('About')}
              </Link>
              <Link to='/blog' style={{ cursor: 'pointer', }}>
                {t('Blog')}
              </Link>
              <Link to='/projects' style={{ cursor: 'pointer', }}>
                {t('Projects')}
              </Link>*/}
            </nav>
          </div>

          <div className="d-none d-lg-block col-auto">
            <SocialLinks className="header__soc" />
          </div>
          <div className="d-none d-lg-block col-auto"><ThemeSwitcher /></div>
          <div className="d-none d-lg-block col-auto"><LangSwitcher /></div>

          <div className="col-auto d-lg-none">
            <MobileMenuIcon isMobileMenuOpen={isMobileMenuOpen} />
          </div>
        </div>
      </div>
    </header>
  );
}
