import React from 'react';
import { useTranslation, Trans } from 'react-i18next';

import { LinkExternal } from '../components/Link';
import {
  TelegramIcon, TwitterIcon, FaceBookIcon, InstagramIcon,
  YouTubeIcon, LinkedInIcon, MediumIcon, TikTokIcon,
} from '../components/Svg';
import {
  links,
} from '../configs';
import './SocialLinks.scss';



const getLinksDefault = (lang = 'en') => [
  { soc: 'telegram', link: links[lang].telegram, },
  { soc: 'twitter', link: links[lang].twitter, },
  { soc: 'linkedin', link: links[lang].linkedin, },
  { soc: 'facebook', link: links[lang].facebook, },
  { soc: 'medium', link: links[lang].medium, },
  { soc: 'youtube', link: links[lang].youtube, },
  { soc: 'instagram', link: links[lang].instagram, },
  { soc: 'tiktok', link: links[lang].tiktok, },
];

function getSocIcon(soc) {
  switch (soc) {
    case 'telegram':
      return (<TelegramIcon />);
    case 'twitter':
      return (<TwitterIcon />);
    case 'facebook':
      return (<FaceBookIcon />);
    case 'instagram':
      return (<InstagramIcon />);
    case 'youtube':
      return (<YouTubeIcon />);
    case 'linkedin':
      return (<LinkedInIcon />);
    case 'medium':
      return (<MediumIcon />);
    case 'tiktok':
      return (<TikTokIcon />);
    default:
      return (null);
  }
}

function SocialLinks({ className, links }) {
  const { t, i18n } = useTranslation('translation');

  if (!links)
    links = getLinksDefault(i18n.language);
  return (
    <div className={"soc " + className}>
      {links.map((item, index) => {
        return (
          <LinkExternal
            key={`${index}`}
            href={item.link}
          >
            {getSocIcon(item.soc)}
          </LinkExternal>
        );
      })}
    </div>
  );
};

export default SocialLinks;
