import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { LocalizedLink, } from 'gatsby-theme-i18n';

import FlagEnIcon from '../assets/images/flags/en.svg';
import FlagRuIcon from '../assets/images/flags/ru.svg';
//import FlagDeIcon from '../assets/images/flags/de.svg';
//import FlagFrIcon from '../assets/images/flags/fr.svg';
import './LangSwitcher.scss';



const list = [
  { code: 'en', name: 'English', localName: 'English', flag: FlagEnIcon, },
  { code: 'ru', name: 'Russian', localName: '', flag: FlagRuIcon, },
  /*{ code: 'de', name: 'German', localName: 'Deutsche', flag: FlagDeIcon, },
  { code: 'fr', name: 'French', localName: 'Franais', flag: FlagFrIcon, },*/
];

function getLanguageData(code = 'en') {
  for (let i in list) {
    if (list[i].code === code) {
      return list[i];
    }
  }
  return list[0];
}


export default function LangSwitcher({ className, }) {
  const { t, i18n } = useTranslation();
  const [data, setData] = useState(getLanguageData());
  useEffect(() => {
    if (data.code !== i18n.language) {
      const dataNew = getLanguageData(i18n.language);
      setData(dataNew);
    }
  }, [i18n.language]);

  const [isOpen, setOpen] = useState(false);
  const onChange = (code) => {
    //i18n.changeLanguage(code);
    setOpen(false);
  }

  return (
    <div className={"lang " + className}>
      <div
        className={`lang__switcher ${isOpen ? 'lang__switcher-active' : ''}`}
        onClick={() => setOpen(!isOpen)}
      >
        <div className="lang__switcher-inner">
          <div className="lang__switcher-flag">
            <img
              src={data.flag}
              alt={`${data.localName} (${data.name})`}
              title={`${data.localName} (${data.name})`}
            />
          </div>
          <div className="lang__switcher-text">
            {i18n.language.toUpperCase()}
          </div>
          <div className="lang__switcher-icon">
            <svg
              width="16"
              height="16"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M8 10.3333C7.82934 10.3333 7.65867 10.268 7.52867 10.138L4.862 7.47133C4.60134 7.21066 4.60134 6.78933 4.862 6.52866C5.12267 6.26799 5.544 6.26799 5.80467 6.52866L8.008 8.73199L10.2033 6.61199C10.4693 6.35666 10.89 6.36399 11.146 6.62866C11.402 6.89333 11.3947 7.31599 11.13 7.57133L8.46334 10.146C8.33334 10.2713 8.16667 10.3333 8 10.3333Z"
                fill="white"
              />
            </svg>
          </div>
        </div>
      </div>

      {isOpen && <div className="lang__drop">
        <div className="lang__drop-inner">
          {list.map((item) => {
            if (item.code === i18n.language)
              return null;

            // TODO to path 
            return (
              <LocalizedLink key={item.code} to='/' language={item.code}>
                <div className="lang__drop-line" onClick={() => onChange(item.code)}>
                  <div className="lang__switcher-flag">
                    <img
                      src={item.flag}
                      alt={`${item.localName} (${item.name})`}
                      title={`${item.localName} (${item.name})`}
                    />
                  </div>
                  <div className="lang__switcher-text">
                    {item.code.toUpperCase()}
                  </div>
                </div>
              </LocalizedLink>
            );
          })}
        </div>
      </div>}
    </div>
  );
}
