import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as ScrollLink, Events as ScrollEvents, } from 'react-scroll';
import { Link, LinkExternal } from './Link';

import SocialLinks from './SocialLinks';
import ThemeSwitcher from './ThemeSwitcher';
import LangSwitcher from './LangSwitcher.jsx';

//import SvgLogo from '../assets/images/logo.svg';
import { LogoIcon } from './Svg';
import './MobileMenu.scss';



function MobileMenu() {
  const { i18n, t } = useTranslation('header');
  //const isMobile = useMediaPredicate(mobileBreakpoint);
  const [isMobileOpen, setMobileOpen] = useState(false);


  useEffect(() => {
    ScrollEvents.scrollEvent.register('begin', function (to, element) {
      setMobileOpen(false);
    });
    return () => {
      ScrollEvents.scrollEvent.remove('begin');
    };
  }, []);
  return (
    <div className="mobilemenu d-lg-none">
      <LangSwitcher className="mobilemenu__langs" />

      <nav className="mobilemenu__nav">
        {/*<Link to='/about' style={{ cursor: 'pointer', }}>
          {t('About')}
        </Link>
        <Link to='/blog' style={{ cursor: 'pointer', }}>
          {t('Blog')}
        </Link>
        <Link to='/projects' style={{ cursor: 'pointer', }}>
          {t('Projects')}
        </Link>*/}
      </nav>

      <ThemeSwitcher className="mobilemenu__theme" />
      <SocialLinks className="mobilemenu__soc" />
    </div>
  );
};

export default MobileMenu;
